// @use 'partials/mixins' as *;


@font-face {
  font-family: "AvenirNextLTPro-Regular";
  src: url("../Font/AvenirNextLTPro-Regular.otf") format("otf");
}



$tablet-breakpoint: 768px;
$desktop-breakpoint: 1200px;

$pri-dark: #323232;
$pri-mid: #afafaf;
$pri-light: #ffffff; 

$sec-dark: #000000;
$sec-mid: #e1e1e1;
$sec-light: #fafafa;

// Text calculations

// Mobile Text Styles

// Page header
// 24px, h1(?), = 1.5
// LH 32px / 2

// Section header
// 24px, h2(?) = 1.5
// LH 32px / 2

// Subheader
// 14px, h3(?) = 
// LH 22px / 0.875

// Body copy
// 13px, h4(?) = 0.813
// LH 18px / 1.125

// Footer copy
// 13px, p(?) = .813
// LH 18px / 1.125

// Labels & Buttons
// 10px, p(styled?) = 0.625
// LH 20px / 1.25

// Desktop & Tablet Text Style

// Page header
// 60px, h1(?) = 3.75
// LH 68px / 4.25

// Section header
// 36px, h2(?) = 2.25
// LH 44px / 2.75


// Subheader
// 24px, h3(?) = 1.5
// LH 32px / 2

// Body copy
// 14px, h4(?) = 1.143
// LH 22px / 1.375

// Footer copy
// 14px, p(?) = 0.875
// LH 20px, 1.25

// Labels & Buttons
// 12px, p(styled?) = 0.75
// LH 20px, 1.25

// Padding calculations

// 80 = 5
// 64 = 4
// 56 = 3.5
// 48 = 3
// 40 = 2.5
// 32 = 2
// 24 = 1.5
// 16 = 1
// 8 = 0.5
// 4 = 0.25

//

body {
}

.body__header {
  width: 100%;
  background-color: $pri-dark;

  p {
    color: $pri-light;
    font-size: 1.5rem;
    text-align: center;
    padding: 1rem;
  font-style: AvenirNextLTPro-Regular;

  }

  ul {
    list-style-type: none;
    width: 50%;
    display: flex;
  }

  ul li {
    width: 100%;
  }
}




